module.exports = "/* CSS talk bubble */\r\n\r\n.talk-bubble {\r\n    /* margin: 40px; */\r\n    display: inline-block;\r\n    position: relative;\r\n    width: 400px;\r\n    top: 0px;\r\n    left: 0px;\r\n    height: auto;\r\n    background-color: lightyellow;\r\n}\r\n\r\n.border {\r\n    border: 8px solid #666;\r\n}\r\n\r\n.round {\r\n    border-radius: 30px;\r\n    -webkit-border-radius: 30px;\r\n    -moz-border-radius: 30px;\r\n}\r\n\r\n/*Right triangle, placed bottom left side slightly in*/\r\n\r\n.tri-right.border.btm-left-in:before {\r\n    content: ' ';\r\n    position: absolute;\r\n    width: 0;\r\n    height: 0;\r\n    left: 30px;\r\n    right: auto;\r\n    top: auto;\r\n    bottom: -40px;\r\n    border: 20px solid;\r\n    border-color: #666 transparent transparent #666;\r\n}\r\n\r\n.tri-right.btm-left-in:after {\r\n    content: ' ';\r\n    position: absolute;\r\n    width: 0;\r\n    height: 0;\r\n    left: 38px;\r\n    right: auto;\r\n    top: auto;\r\n    bottom: -20px;\r\n    border: 12px solid;\r\n    border-color: lightyellow transparent transparent lightyellow;\r\n}\r\n\r\n.pad-50 {\r\n    padding-top: 50px;\r\n}\r\n\r\n#intro {\r\n    color: #000;\r\n    padding: 20px;\r\n    font-size: 14px;\r\n    font-weight: 700;\r\n}\r\n\r\n#showMessage button,\r\n.dynamicBtn {\r\n    border-radius: 20px;\r\n    padding: 10px;\r\n    background: none;\r\n    color: #000;\r\n    border: 0;\r\n    background-color: #e5e4e9;\r\n    border-radius: 20px;\r\n    margin: 0 10px;\r\n    font-weight: 700;\r\n    font-size: 14px;\r\n    width: 200px;\r\n}\r\n\r\nselect {\r\n    border-radius: 5px;\r\n    background-color: rgb(7, 45, 49);\r\n    border: 0px;\r\n    color: rgb(7, 45, 49);\r\n    display: block;\r\n    font-size: 10px;\r\n    font-family: sans-serif;\r\n    font-weight: 700;\r\n    line-height: 1.3;\r\n    padding: .6em 1.4em .5em .8em;\r\n    width: 18%;\r\n    max-width: 100%;\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n}\r\n\r\n#buttonOpt {\r\n    background-color: #4CAF50;\r\n    border: 1px solid #fff;\r\n    color: white;\r\n    /* padding: 10px 24px; */\r\n    cursor: pointer;\r\n    height: 70px;\r\n    text-transform: uppercase;\r\n    width: 300px;\r\n    border-radius: 48px;\r\n    margin: 20px;\r\n}\r\n\r\n.buttonGlow {\r\n    background-color: lightyellow;\r\n    border-radius: 10px;\r\n    border: none;\r\n    color: #000;\r\n    cursor: pointer;\r\n    display: inline-block;\r\n    font-family: Arial;\r\n    font-size: 20px;\r\n    padding: 20px;\r\n    text-align: center;\r\n    border: 4px solid #666;\r\n    text-decoration: none;\r\n    -webkit-animation: glowing 1500ms infinite;\r\n    animation: glowing 1500ms infinite;\r\n}\r\n\r\n@-webkit-keyframes glowing {\r\n    0% {\r\n        background-color: #ffffe0;\r\n        -webkit-box-shadow: 0 0 3px #ffffe0;\r\n    }\r\n    50% {\r\n        background-color: #ffffe0;\r\n        -webkit-box-shadow: 0 0 40px #ffffe0;\r\n    }\r\n    100% {\r\n        background-color: #ffffe0;\r\n        -webkit-box-shadow: 0 0 3px #ffffe0;\r\n    }\r\n}\r\n\r\n@keyframes glowing {\r\n    0% {\r\n        background-color: #ffffe0;\r\n        box-shadow: 0 0 3px #ffffe0;\r\n    }\r\n    50% {\r\n        background-color: #ffffe0;\r\n        box-shadow: 0 0 40px #ffffe0;\r\n    }\r\n    100% {\r\n        background-color: #ffffe0;\r\n        box-shadow: 0 0 3px #ffffe0;\r\n    }\r\n}\r\n\r\n.bg-image {\r\n    background-color: rgb(7, 45, 49);\r\n    /* position: fixed; */\r\n    /* background-position: center;\r\n    background-repeat: no-repeat;\r\n    background-size: cover; */\r\n    height: 100%;\r\n}\r\n\r\n.margin0 {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.image-container img {\r\n    /* float: right; */\r\n    top: 20px;\r\n    position: relative;\r\n}"